import React, { Component } from "react";

class TodoForm extends Component {
    constructor () {
      super();
      this.state = {
        title: '',
        responsible: '',
        description: '',
        priority: 'low'
      };
      this.handleInputChange = this.handleInputChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    handleSubmit(e) {
      e.preventDefault();
      this.props.onAddTodo(this.state);
      this.setState({
        title: '',
        responsible: '',
        description: '',
        priority: 'low'
      });
    }
  
    handleInputChange(e) {
      const {value, name} = e.target;
      this.setState({
        [name]: value
      });
    }
    openMenu(e){
        e.preventDefault();
        document.querySelector(".mi-class__form-f").style.height = "0";   
    }
  
    render() {
      return (
        <div className="mi-class__form-f card">
          <form onSubmit={this.handleSubmit} className="mi-class__form-d card-body">
                <button onClick={this.openMenu} className="mi-class__button-x btn btn-danger">
                    x
                </button>
            <div className="form-group">
              <input
                type="text"
                name="title"
                className="form-control"
                value={this.state.title}
                onChange={this.handleInputChange}
                placeholder="Title"
                />
            </div>
            <div className="form-group">
              <input
                type="text"
                name="responsible"
                className="form-control"
                value={this.state.responsible}
                onChange={this.handleInputChange}
                placeholder="Responsible"
                />
            </div>
            <div className="form-group">
              <input
                type="text"
                name="description"
                className="form-control"
                value={this.state.description}
                onChange={this.handleInputChange}
                placeholder="Description"
                />
            </div>
            <div className="form-group">
              <select
                  name="priority"
                  className="form-control"
                  value={this.state.priority}
                  onChange={this.handleInputChange}
                >
                <option>low</option>
                <option>medium</option>
                <option>high</option>
              </select>
            </div>
            <button type="submit" className="mi-class__button-s btn btn-primary">
              Save
            </button>
          </form>
        </div>
      )
    }
  
  }
export default TodoForm